.education-container {
    padding: 40px;
    background-color: #e3e3e3;
  }
  
  .education-heading {
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
  }
  
  .education-icon {
    width: 60px;
    margin-left: 10px;
    vertical-align: middle;
  }
  
  .education-list {
    display: flex;
    flex-direction: column;
    margin-left: 330px;
    gap: 20px;
    width: 1000px;
  }
  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 60%;
    margin: 0 auto 30px;
  }
  .education-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border: 2px solid #0000ff;
    border-radius: 10px;
  }
  
  .education-details {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .bullet-point {
    width: 12px;
    height: 12px;
    background-color: #0000ff;
    border-radius: 50%;
    flex-shrink: 0;
  }
  
  .education-year {
    font-size: 18px;
    font-weight: bold;
    color: #ffffff;
    background-color: #0000ff;
    padding: 10px 20px;
    border-radius: 20px;
  }
  
  h3 {
    font-size: 20px;
    color: #3c0f99;
    margin: 0;
  }
  
  .sub-title {
    font-size: 16px;
    color: #f57c00;
    margin: 5px 0;
  }
  
  .score {
    font-size: 16px;
    color: #000;
    font-weight: bold;
  }

  @media (max-width: 768px) {
    .education-container {
      padding: 20px;
      height: 770px; /* Reduced padding for mobile */
    }
  
    .education-heading {
      font-size: 2rem; /* Adjusted heading size for mobile */
      text-align: center;
      margin-bottom: 20px;
    }
  
    .education-icon {
      width: 40px; /* Adjusted icon size for mobile */
      margin-left: 5px;
    }
  
    .education-list {
      position: relative;
      right: 10px;
      margin-left: 0; /* Removed left margin */
      gap: 15px; /* Reduced gap between items */
      width: 100%; /* Make the list take full width */
      padding: 0 10px; /* Add some padding to avoid content touching the screen edges */
    }
  
    .education-item {
      display: flex;
      flex-direction: column; /* Stack the items vertically on mobile */
      justify-content: flex-start;
      align-items: flex-start;
      padding: 15px;
      border: 1px solid #0000ff;
      border-radius: 8px; /* Reduced border radius for mobile */
      margin-bottom: 15px; /* Space between items */
    }
  
    .education-details {
      display: block; /* Stacked layout for details on mobile */
      margin-bottom: 10px;
    }
  
    .bullet-point {
      width: 10px; /* Smaller bullet points */
      height: 10px;
      background-color: #0000ff;
      border-radius: 50%;
      flex-shrink: 0;
      margin-bottom: 5px; /* Space between bullet and text */
    }
  
    .education-year {
      font-size: 14px; /* Smaller year text */
      font-weight: bold;
      color: #ffffff;
      background-color: #0000ff;
      padding: 8px 15px;
      border-radius: 15px;
      margin-bottom: 10px; /* Added margin for spacing */
    }
  
    h3 {
      font-size: 1.2rem; /* Reduced size for mobile */
      color: #3c0f99;
      margin: 0;
    }
  
    .sub-title {
      font-size: 1rem; /* Smaller subtitle text */
      color: #f57c00;
      margin: 5px 0;
    }
  
    .score {
      font-size: 1rem; /* Reduced score size */
      color: #000;
      font-weight: bold;
    }
  }
  