.skills-section {
    text-align: center;
    background-color: #e3e3e3;
    padding: 20px;
    height: 600px;
    /* margin-bottom: 10px; */
  }
  
  .skills-section h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .skills-section p {
    font-size: 1.2rem;
    color: gray;
    margin-bottom: 20px;
  }
  
  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 60%;
    margin: 0 auto 30px;
  }
  
  .skills-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    max-width: 800px;
    margin: 0 auto;
    
  }
  
  .skill-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .skill-dot {
    height: 12px;
    width: 12px;
    background-color: #007bff;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .skill-name {
    font-size: 1.5rem;
    font-weight: bold;
    color: orange;
    flex: 1;
    text-align: left;
  }
  
  .skill-bar {
    position: relative;
    top: 20px;
    margin-bottom: 40px;
    flex: 2;
    height: 20px;
    background-color: #444;
    overflow: hidden;
    margin-left: 10px;
  }
  
  .skill-level {
    height: 100%;
    background-color: #007bff;
  }
  /* Mobile-specific styles */
@media (max-width: 768px) {
  /* Skills Section */
  .skills-section {
    text-align: center;
    background-color: #e3e3e3;
    padding: 20px;
    min-height: 600px;
  }

  .skills-section h1 {
    font-size: 2rem; /* Adjusted font size for mobile */
    margin-bottom: 10px;
  }

  .skills-section p {
    font-size: 1rem; /* Adjusted font size for mobile */
    color: gray;
    margin-bottom: 20px;
  }

  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 80%; /* Increased width for mobile */
    margin: 0 auto 30px;
  }

  .skills-container {
    display: grid;
    grid-template-columns: (2,1fr); 
    gap: 20px; 
    max-width: 100%;
    margin: 0 auto;
    margin-left: 50px;
  }

  .skill-item {
    display: flex;
    flex-direction: column; 
    align-items: flex-start;
    justify-content: space-between;
  }

  .skill-name {
    font-size: 1.2rem; 
    font-weight: bold;
    color: orange;
    flex: 1;
    text-align: left;
  }

  .skill-bar {
    position: relative;
    margin-top: 10px;
    flex: 2;
    height: 15px; 
    background-color: #8f8f8f;
    overflow: hidden;
    margin-left: 0; 
    width: 100%; 
  }

  .skill-level {
    height: 100%;
    background-color: #007bff;
  }
}
