.about {
    font-family: Arial, sans-serif;
    padding: 10px 20px;
    background-color: #dedede;
    height: 800px;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 90%;
    margin: 0 auto 30px;
  }
  
  .about-heading {
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 30px;
  }
  
  .about-content {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  
  .about-image img {
    width: 400px;
    height: 550px;
    border-radius: 10px;
  }
  
  .about-highlights {
    flex: 1;
  }
  
  .about-highlights h3 {
    position: relative;
    bottom: 30px;
    color: #ff5722;
    font-size: 32px;
    margin-bottom: 20px;
  }
  
  .about-highlights ul {
    list-style: none;
    padding: 0;
    margin: 0 0 100px 0;
  }
  
  .about-highlights li {
    display: flex;
    align-items: center;
    font-size: 18px;
    margin-bottom: 50px;
  }
  
  .about-highlights .icon {
    font-size: 20px;
    color: #0066ff;
    margin-right: 10px;
  }
  
  .buttons {
    margin-top: 20px;
  }
  
  .btn {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .hire-me {
    background-color: #0066ff;
    margin-right: 10px;
  }
  
  .resume {
    background-color: #0066ff;
  }
  
@media (max-width: 768px) {

  .about {
    font-family: Arial, sans-serif;
    padding: 20px;
    background-color: #dedede;
    min-height: 1020px;
    margin: 5px;
  }

 
  .container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 10px;
  }

  .skills-divider {
    border: 0;
    height: 1px;
    background: #007bff;
    width: 90%;
    margin: 0 auto 30px;
  }


  .about-heading {
    text-align: center;
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 20px;
    font-family: "Sen",sans-serif;
    margin-bottom: 20px;
  }
  .about-heading h1{
    
  position: relative;
  bottom: 10px;
}


  .about-content {
    display: flex;
    flex-direction: column; 
    align-items: center;
    gap: 20px; 
  }
  .about-image img {
    width: 100%;
    max-width: 100%; 
    height: 350px;
    border-radius: 10px;
    margin-bottom: 10px;
  }

  .about-highlights {
    text-align: justify;
    padding: 0 20px;
  }

  .about-highlights h3 {
    color: #ff5722;
    font-size: 24px; 
    margin-bottom: 15px;
  }

  .about-highlights ul {
    list-style: none;
    padding: 0;
    margin-bottom: 50px;
  }

  .about-highlights li {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px; /* Smaller font size for mobile */
    margin-bottom: 30px;
  }

  .about-highlights .icon {
    font-size: 18px;
    color: #0066ff;
    margin-right: 10px;
  }
  .buttons {
    text-align: center;
    margin-top: 20px;
  }

  .btn {
    padding: 10px 20px;
    font-size: 14px;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }

  .hire-me, .resume {
    background-color: #0066ff;
  }
}
